      stressLevel: "",
      depressionLevelBm: "",
      anxietyLevelBm: "",
      stressLevelBm: "",
      depressionDesc: "",
      anxietyDesc: "",
      stressDesc: "",
      depressionDescBm: "",
      anxietyDescBm: "",
      stressDescBm: "",
      depressionColour: "",
      anxietyColour: "",
      stressColour: "",
      depressionWidth: [],
      anxietyWidth: [],
      stressWidth: [],
      depressionLabel: [],
      anxietyLabel: [],
      stressLabel: [],
      depressionLabelBm: [],
      anxietyLabelBm: [],
      stressLabelBm: [],

      model: {
        depression: [],
        anxiety: [],
        stress: [],
      },
      schema: {
        fields: [
          {
            type: "radios",
            label:
              "<ol start = '1'><li class='label-width'>I found it hard to wind down.<span class='requiredLabel'> *</span><br><span class='malay'>Saya dapati diri saya sukar ditenteramkan.</span></li></ol>",
            model: "stress[0]",
            values: [0, 1, 2, 3],
