            model: "anxiety[4]",
            values: [0, 1, 2, 3],
            required: true,
            validator: "required",
            styleClasses: "odd-row",
          },
          {
            type: "radios",
            label:
              "<ol start = '16'><li class='label-width'>I was unable to become enthusiastic about anything.<span class='requiredLabel'> *</span><br><span class='malay'>Saya tidak bersemangat dengan apa jua yang saya lakukan.</span></li></ol>",
            model: "depression[4]",
            values: [0, 1, 2, 3],
            required: true,
            validator: "required",
            styleClasses: "even-row",
          },
          {
            type: "radios",
            label:
              "<ol start = '17'><li class='label-width'>I felt I wasn't worth much as a person.<span class='requiredLabel'> *</span><br><span class='malay'>Saya rasa tidak begitu berharga sebagai seorang individu.</span></li></ol>",
            model: "depression[5]",
            values: [0, 1, 2, 3],
            required: true,
            validator: "required",
            styleClasses: "odd-row",
          },
          {
            type: "radios",
            label:
              "<ol start = '18'><li class='label-width'>I felt that I was rather touchy.<span class='requiredLabel'> *</span><br><span class='malay'>Saya rasa saya mudah tersentuh.</span></li></ol>",
            model: "stress[6]",
            values: [0, 1, 2, 3],
            required: true,
            validator: "required",
            styleClasses: "even-row",
          },
          {
            type: "radios",
            label:
              "<ol start = '19'><li class='label-width'>I was aware of the action of my heart in the absence of physical exertion (eg, sense <br>of heart rate increase, heart missing a beat).<span class='requiredLabel'> *</span><br><span class='malay'>Saya sedar tindakbalas jantung saya walaupun tidak melakukan aktiviti fizikal (contohnya kadar <br>denyutan jantung bertambah, atau denyutan jantung berkurangan).</span></li></ol>",
