        this.stressDesc =
          "Based on your responses, there is a chance that you are currently experiencing stress. We suggest that you book an appointment with professionals, you can go either to MENTARI Consultation Clinic, your GP or Public Health Clinic to discuss these results with them. Take a printed copy of this report with you and book an appointment so that you can discuss your current symptoms and your future plan of management with them. \r\n\r\n";
        this.stressLevelBm = "Sederhana";
        this.stressDescBm =
