        this.anxietyColour = "#EED14A";
      } else if (this.anxietyScore >= 9 && this.anxietyScore < 10 + 1) {
        this.anxietyLevel = "Severe";
        this.anxietyDesc =
          "Based on your responses, there is a chance that you are currently experiencing anxiety. We suggest that you book an appointment with professionals, you can go either to MENTARI Consultation Clinic, your GP or Public Health Clinic to discuss these results with them. Take a printed copy of this report with you and book an appointment so that you can discuss your current symptoms and your future plan of management with them.";
        this.anxietyLevelBm = "Teruk";
        this.anxietyDescBm =
          "Berdasarkan jawapan anda, anda berkemungkinan sedang mengalami kegelisahan. Cadangan kepada anda, sila dapatkan temujanji bersama profesional, anda boleh mendapatkannya sama ada di MENTARI, doktor (GP) anda atau di Klinik Kesihatan yang berhampiran untuk membincangkan laporan ujian ini. Dapatkan laporan ujian ini dan bawa semasa temujanji anda untuk membincangkan gejala anda yang terkini dan membincangkan rawatan lanjut bersama mereka.";
        this.anxietyColour = "#EE9B4A";
      } else {
        this.anxietyLevel = "Extremely Severe";
        this.anxietyDesc =
          "Based on your responses, there is a chance that you are currently experiencing anxiety. We suggest that you book an appointment with professionals, you can go either to MENTARI Consultation Clinic, your GP or Public Health Clinic to discuss these results with them. Take a printed copy of this report with you and book an appointment so that you can discuss your current symptoms and your future plan of management with them.";
        this.anxietyLevelBm = "Sangat Teruk";
        this.anxietyDescBm =
          "Berdasarkan jawapan anda, anda berkemungkinan sedang mengalami kegelisahan. Cadangan kepada anda, sila dapatkan temujanji bersama profesional, anda boleh mendapatkannya sama ada di MENTARI, doktor (GP) anda atau di Klinik Kesihatan yang berhampiran untuk membincangkan laporan ujian ini. Dapatkan laporan ujian ini dan bawa semasa temujanji anda untuk membincangkan gejala anda yang terkini dan membincangkan rawatan lanjut bersama mereka.";
        this.anxietyColour = "#EE5D4A";
      }

      this.anxietyWidth[0] = parseFloat((((4 - 0 + 1) / 22) * 100).toFixed(2));
      this.anxietyWidth[1] = parseFloat((((6 - 5 + 1) / 22) * 100).toFixed(2));
      this.anxietyWidth[2] = parseFloat((((8 - 7 + 1) / 22) * 100).toFixed(2));
      this.anxietyWidth[3] = parseFloat((((10 - 9 + 1) / 22) * 100).toFixed(2));
      this.anxietyWidth[4] = parseFloat(
