        "Normal",
        "Mild",
        "Moderate",
        "Severe",
        "Extremely Severe",
      ];
      this.anxietyLabelBm = [
        "Normal",
        "Ringan",
        "Sederhana",
        "Teruk",
        "Sangat Teruk",
      ];

      if (this.depressionScore >= 0 && this.depressionScore < 5 + 1) {
        this.depressionLevel = "Normal";
        this.depressionDesc =
          "Based on your responses, you are currently experiencing low level of depression. Recommended for you, if you would like more information regarding mental health issue, please go to MENTARI Portal or reach out to the nearest MENTARI in our directory.";
        this.depressionLevelBm = "Normal";
        this.depressionDescBm =
          "Berdasarkan jawapan anda, anda sedang mengalami kemurungan yang rendah. Cadangan kepada anda, sekiranya anda ingin mendapatkan maklumat lanjut berkaitan masalah kesihatan mental, sila layari Portal MENTARI atau hubungi MENTARI yang terdekat melalui direktori kami.";
        this.depressionColour = "#CDEE4A";
      } else if (this.depressionScore >= 6 && this.depressionScore < 7 + 1) {
        this.depressionLevel = "Mild";
        this.depressionDesc =
          "Based on your responses, you are currently experiencing low level of depression. Recommended for you, if you would like more information regarding mental health issue, please go to MENTARI Portal or reach out to the nearest MENTARI in our directory.\r\n";
        this.depressionLevelBm = "Ringan";
        this.depressionDescBm =
          "Berdasarkan jawapan anda, anda sedang mengalami kemurungan yang rendah. Cadangan kepada anda, sekiranya anda ingin mendapatkan maklumat lanjut berkaitan masalah kesihatan mental, sila layari Portal MENTARI atau hubungi MENTARI yang terdekat melalui direktori kami.";
        this.depressionColour = "#EEE84A";
      } else if (this.depressionScore >= 8 && this.depressionScore < 10 + 1) {
        this.depressionLevel = "Moderate";
        this.depressionDesc =
          "Based on your responses, there is a chance that you are currently experiencing depression. We suggest that you book an appointment with professionals, you can go either to MENTARI Consultation Clinic, your GP or Public Health Clinic to discuss these results with them. Take a printed copy of this report with you and book an appointment so that you can discuss your current symptoms and your future plan of management with them.";
        this.depressionLevelBm = "Sederhana";
        this.depressionDescBm =
          "Berdasarkan jawapan anda, anda berkemungkinan sedang mengalami kemurungan. Cadangan kepada anda, sila dapatkan temujanji bersama profesional, anda boleh mendapatkannya sama ada di MENTARI, doktor (GP) anda atau di Klinik Kesihatan yang berhampiran untuk membincangkan laporan ujian ini. Dapatkan laporan ujian ini dan bawa semasa temujanji anda untuk membincangkan gejala anda yang terkini dan membincangkan rawatan lanjut bersama mereka.";
        this.depressionColour = "#EED14A";
      } else if (this.depressionScore >= 11 && this.depressionScore < 14 + 1) {
        this.depressionLevel = "Severe";
        this.depressionDesc =
          "Based on your responses, there is a chance that you are currently experiencing depression. We suggest that you book an appointment with professionals, you can go either to MENTARI Consultation Clinic, your GP or Public Health Clinic to discuss these results with them. Take a printed copy of this report with you and book an appointment so that you can discuss your current symptoms and your future plan of management with them.";
        this.depressionLevelBm = "Teruk";
        this.depressionDescBm =
          "Berdasarkan jawapan anda, anda berkemungkinan sedang mengalami kemurungan. Cadangan kepada anda, sila dapatkan temujanji bersama profesional, anda boleh mendapatkannya sama ada di MENTARI, doktor (GP) anda atau di Klinik Kesihatan yang berhampiran untuk membincangkan laporan ujian ini. Dapatkan laporan ujian ini dan bawa semasa temujanji anda untuk membincangkan gejala anda yang terkini dan membincangkan rawatan lanjut bersama mereka.";
        this.depressionColour = "#EE9B4A";
      } else {
        this.depressionLevel = "Extremely Severe";
        this.depressionDesc =
          "Based on your responses, there is a chance that you are currently experiencing depression. We suggest that you book an appointment with professionals, you can go either to MENTARI Consultation Clinic, your GP or Public Health Clinic to discuss these results with them. Take a printed copy of this report with you and book an appointment so that you can discuss your current symptoms and your future plan of management with them.";
        this.depressionLevelBm = "Sangat";
        this.depressionDescBm =
          "Berdasarkan jawapan anda, anda berkemungkinan sedang mengalami kemurungan. Cadangan kepada anda, sila dapatkan temujanji bersama profesional, anda boleh mendapatkannya sama ada di MENTARI, doktor (GP) anda atau di Klinik Kesihatan yang berhampiran untuk membincangkan laporan ujian ini. Dapatkan laporan ujian ini dan bawa semasa temujanji anda untuk membincangkan gejala anda yang terkini dan membincangkan rawatan lanjut bersama mereka.";
        this.depressionColour = "#EE5D4A";
      }

      this.depressionWidth[0] = parseFloat(
        (((5.0 - 0.0 + 1) / 22) * 100).toFixed(2)
      );
      this.depressionWidth[1] = parseFloat(
        (((7 - 6 + 1) / 22) * 100).toFixed(2)
      );
      this.depressionWidth[2] = parseFloat(
        (((10 - 8 + 1) / 22) * 100).toFixed(2)
      );
      this.depressionWidth[3] = parseFloat(
        (((14 - 11 + 1) / 22) * 100).toFixed(2)
      );
      this.depressionWidth[4] = parseFloat(
        (((21 - 15 + 1) / 22) * 100).toFixed(2)
      );

      this.depressionLabel = [
        "Normal",
        "Mild",
        "Moderate",
        "Severe",
        "Extremely Severe",
      ];
      this.depressionLabelBm = [
        "Normal",
        "Ringan",
        "Sederhana",
        "Teruk",
        "Sangat Teruk",
      ];

      let data = {
        stress: {
          name: "STRESS",
          nameBm: "TEKANAN",
          score: this.stressScore,
          level: this.stressLevel,
          desc: this.stressDesc,
          levelBm: this.stressLevelBm,
          descBm: this.stressDescBm,
          colour: this.stressColour,
          width: this.stressWidth,
          label: this.stressLabel,
          labelBm: this.stressLabelBm,
        },
        anxiety: {
          name: "ANXIETY",
          nameBm: "KEGELISAHAN",
          score: this.anxietyScore,
          level: this.anxietyLevel,
          desc: this.anxietyDesc,
          levelBm: this.anxietyLevelBm,
          descBm: this.anxietyDescBm,
          colour: this.anxietyColour,
          width: this.anxietyWidth,
          label: this.anxietyLabel,
          labelBm: this.anxietyLabelBm,
        },
        depression: {
          name: "DEPRESSION",
          nameBm: "KEMURUNGAN",
          score: this.depressionScore,
          level: this.depressionLevel,
          desc: this.depressionDesc,
          levelBm: this.depressionLevelBm,
          descBm: this.depressionDescBm,
          colour: this.depressionColour,
          width: this.depressionWidth,
          label: this.depressionLabel,
          labelBm: this.depressionLabelBm,
        },
      };

      this.$router.push({
        path: "/dass-results",
        query: { id: sessionStorage.setItem("dassData", JSON.stringify(data)) },
      });

      const testOnly = new FormData();
      testOnly.append("type", "2");
      testOnly.append(
        "score",
        this.stressScore + "-" + this.anxietyScore + "-" + this.depressionScore
      );
      //this.$axios
      //  // .post("http://127.0.0.1:8000/api/postTest", testOnly)
      //  // .post('https://10.22.120.108:8000/api/postTest', testOnly)
      //  .then((response) => {
      //    this.$router.push({
      //      path: "/dass-results",
      //      query: { id: response.data.id },
      //    });
      //  });
    },
  },
  //created() {
  //  this.$axios
  //    .get("http://127.0.0.1:8000/api/getTestRange?type=2")
  //    // this.$axios.get("https://10.22.120.108:8000/api/getTestRange?type=2")
  //    .then((response) => (this.rangeInfo = response.data.data))
  //    .catch((error) => {
  //      console.error("There was an error!", error);
  //    });
  //},
};
</script>

<style>
#dass {
  text-align: center;
  font-weight: bold;
  margin-bottom: 50px;
}
h4,
h6 {
  text-align: center;
}
hr {
  margin: 40px 0px 40px 0px;
}
#test {
  box-shadow: 0px 10px 15px #6b6b6b9c;
  padding: 40px 40px 100px 40px;
  border-radius: 20px;
  margin-bottom: 50px;
}
.malay {
  font-style: italic;
  font-size: small;
  color: dimgrey;
}
.malayHeader {
  font-style: italic;
  font-size: small;
  text-align: center;
  padding-left: 50px;
  padding-right: 50px;
}
.label-width {
  width: 700px;
}
div.errors.help-block {
  display: none;
  width: 0px;
}
.error-field {
  background: #f9c1c6;
}
.form-group.required > label:after {
  display: none;
  width: 0px;
}
.requiredLabel {
  color: red;
}
#num {
  max-height: 30px;
  margin: 10px 20px;
  text-align: center;
  padding: 15px 12px;
  background: #eee;
  border-radius: 20px;
  border-color: grey;
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: bold;
  color: #777;
  display: flex;
  align-items: center;
  justify-content: center;
}
.r {
  display: inline-flex;
  width: 100%;
  align-items: center;
}
.indicator {
  margin: 10px 0px;
}
.scale {
  margin: 0px 15px 40px 15px;
}
.vue-form-generator .field-wrap {
  align-items: center;
}
@media (max-width: 760px) {
  #test {
    padding: 40px 20px 100px 20px;
    margin-bottom: 50px;
  }
  .label-width {
    width: 100%;
  }
}
@media (min-width: 768px) and (max-width: 992px) {
  #test {
    padding: 40px 20px 100px 20px;
    margin-bottom: 50px;
  }
  .label-width {
    width: 320px;
  }
}
@media (min-width: 992px) and (max-width: 1200px) {
  #test {
    padding: 40px 20px 100px 20px;
    margin-bottom: 50px;
  }
  .label-width {
    width: 500px;
  }
}
</style>



